export const getDeepData = (data) => {
  if (data === undefined) {
    return []
  }
  let res = []
  data.forEach((item) => {
    if (item.children === undefined) {
      res.push(item.id)
    }
    res = [...res, ...getDeepData(item.children)]
  })
  return res
}
export const getPointedData = (data, layer) => {
  let curLayer = 0

  const recTree = (data, layer) => {
    const lay = layer
    let res = []
    if (data === undefined) {
      curLayer--
      return []
    }
    if (curLayer === lay) {
      curLayer--
      return (res = [...data])
    }
    data.forEach((item) => {
      curLayer++
      res = [...res, ...recTree(item.children, lay)]
    })
    curLayer--
    return res
  }
  return recTree(data, layer)
}
