import request from './request'

export const getAllParams = (id, sel) => {
  return request({
    url: `categories/${id}/attributes`,
    method: 'get',
    params: { sel: sel }
  })
}
export const addParam = (id, data) => {
  return request({
    url: `categories/${id}/attributes`,
    method: 'post',
    data
  })
}
export const deleParam = (id, attrid) => {
  return request({
    url: `categories/${id}/attributes/${attrid}`,
    method: 'delete'
  })
}
export const getParam = (id, attrid) => {
  return request({
    url: `categories/${id}/attributes/${attrid}`,
    method: 'get'
  })
}
export const editParam = (id, attrid, data) => {
  return request({
    url: `categories/${id}/attributes/${attrid}`,
    method: 'put',
    data
  })
}
