import { ElMessage, ElMessageBox } from 'element-plus'
export const deletes = (callback1, init, i18n) => {
  ElMessageBox.confirm(i18n.t('dialog.deleteTitle'), '提示', {
    confirmButtonText: 'OK',
    cancelButtonText: 'Cancel',
    type: 'warning'
  })
    .then(async () => {
      await callback1()
      ElMessage({
        type: 'success',
        message: 'Delete completed'
      })
      init()
    })
    .catch(() => {
      ElMessage({
        type: 'info',
        message: 'Delete canceled'
      })
    })
}
